<template>
  <div :style="{width:boxWidth+'px'}" class="vdate-pc">
    <div class="vdate-header" :style="{borderColor:borderColor}">
      <div class="header-black">
        <div @click="yearBtn('upper')" style="margin-right: 15px;">上一年</div>
        <div @click="monthBtn('upper')">上月</div>
      </div>
      <div class="header-black">
        <div>{{newYear}}月</div>
        <div>{{newMonth}}日</div>
      </div>
      <div class="header-black">
        <div @click="monthBtn('lower')" style="margin-right: 15px;">下月</div>
        <div @click="yearBtn('lower')">下一年</div>
      </div>
    </div>
    <div class="vdate-content" :style="{borderColor:borderColor}">
      <div
        class="v-date-balck"
        v-for="(item,inx) in days"
        :key="inx"
        :style="{borderColor:borderColor}"
        :class="{'prohibit': inspectName(item)}"
        @click="balckBtn(item,inx)"
      >
        <div>{{formatDate(item.dateline,true)}}</div>
        <div>原价:xxx</div>
        <div>售价:xxx</div>
        <div>库存:xxx</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "vdate-pc",
    props: {
      boxWidth: {
        // 盒子的宽度
        type: Number,
        default: () => {
          return 800;
        }
      },
      borderColor: {
        // 边框颜色
        type: String,
        default: () => {
          return "#ccc";
        }
      },
      disableStyle: {
        // 禁止的样式
        type: String,
        default: () => {
          return "color:#000";
        }
      },
      initDate: {
        // 初始化日期---2020-05-01
        type: String,
        default: () => {
          return "2020-05-01";
        }
      },
      options: {
        // 日历中每天的配置项
        type: Array,
        default: () => {
          return [
            {
              key:'price',
              name:'价格'
            }
          ];
        }
      }
    },
    data() {
      return {
        newYear: null,
        newMonth: null,
        newDay: null,
        days: [],
        leftYear: "<<",
        leftMonth: "<"
      };
    },
    computed: {},
    watch: {},
    created() {
      this.init(this.initDate);
    },
    mounted() {},
    methods: {
      inspectName(item) {
        let newType =
          item.dateline.getFullYear() != this.newYear ||
          item.dateline.getMonth() + 1 != this.newMonth;
        return newType;
      },
      formatDate(data, type = false) {
        let y, m;
        let d = 1;
        if (data) {
          y = new Date(data).getFullYear();
          m = new Date(data).getMonth() + 1;
          if (type) {
            d = new Date(data).getDate();
          }
        } else {
          y = new Date().getFullYear();
          m = new Date().getMonth() + 1;
        }
        if (m < 10) m = "0" + m;
        return y + "-" + m + "-" + d;
      },
      init(val) {
        if (!val) {
          // 取这个月的一号
          console.log("取本月的一号", this.formatDate(val));
        } else {
          console.log("穿值了", this.formatDate(val));
        }
        this.initDataArr(this.formatDate(val));
      },
      balckBtn(val, index) {
        if (this.inspectName(val)) {
          console.log("xxx");
        } else {
          console.log(index);
        }
      },
      initDataArr(cur) {
        // 生成日历数据
        // cur --- 2020-05-01
        let date;
        if (cur) {
          date = new Date(cur);
        } else {
          let d = new Date(this.formatDate());
          d.setDate(42);
          date = new Date(this.formatDate());
        }
        this.newYear = date.getFullYear();
        this.newMonth = date.getMonth() + 1;
        this.newDay = date.getDate();
        let newWeek = date.getDay(); // 1...6,0 取上个月还存有多少天，这里取的是这个月一号的getDay
        // eslint-disable-next-line
        if (newWeek == 0) {
          newWeek = 7;
        }
        let str = this.formatDate(cur);
        this.days.length = 0;
        // 获取上个月的时间
        for (let i = newWeek - 1; i >= 0; i--) {
          let d = new Date(str);
          d.setDate(d.getDate() - i); // setDate返回距离一号调整过的日期的毫秒
          let dayObj = {}; // 用一个对象包装Date对象  以便为以后预定功能添加属性 --- 看自己实际情况而定
          dayObj.dateline = d;
          this.days.push(dayObj); // 将日期放入data 中的days数组 供页面渲染使用
        }
        // 获取本月以及下个月的时间
        for (let i = 1; i <= 42 - newWeek; i++) {
          let d = new Date(str);
          d.setDate(d.getDate() + i);
          let dayObj = {}; // 用一个对象包装Date对象  以便为以后预定功能添加属性 --- 看自己实际情况而定
          dayObj.dateline = d;
          this.days.push(dayObj);
        }
        this.days.sort((a, b) => {
          // 利用array的sort进行一个排序刚好利用时间戳把日期排序好
          // console.log(
          //   a.dateline.getFullYear(),
          //   a.dateline.getMonth() + 1,
          //   a.dateline.getDate(),
          //   "-------",
          //   b.dateline.getFullYear(),
          //   b.dateline.getMonth() + 1,
          //   b.dateline.getDate()
          // );
          return a.dateline.getTime() - b.dateline.getTime();
        });
        // console.log(this.newYear, this.newMonth);
      },
      // 月份点击
      monthBtn(type) {
        console.log(this.newYear,this.newMonth)
        if(!this.newYear || !this.newMonth) {
          return false
        }
        let newStr = "";
        // 判断是否点击上个月
        if (type === "upper") {
          // 判断是否为一月
          if (this.newMonth === 1) {
            newStr = `${this.newYear - 1}-12-1`;
          } else {
            newStr = `${this.newYear}-${this.newMonth - 1}-1`;
          }
        } else {
          // 判断是否为十二月
          if (this.newMonth === 12) {
            newStr = `${this.newYear + 1}-01-1`;
          } else {
            newStr = `${this.newYear}-${this.newMonth + 1}-1`;
          }
        }
        this.initDataArr(newStr);
      },
      // 年份点击
      yearBtn(type) {
        console.log(this.newYear,this.newMonth)
        if(!this.newYear || !this.newMonth) {
          return false
        }
        let newStr = "";
        // 判断是否点击上一年
        if (type === "upper") {
          newStr = `${this.newYear - 1}-${this.newMonth}-1`;
        } else {
          newStr = `${this.newYear + 1}-${this.newMonth}-1`;
        }
        this.initDataArr(newStr);
      }
    },
    beforeDestroy() {}
  };
</script>
<style lang='scss' scoped>
  div,
  span,
  p {
    box-sizing: border-box;
  }
  .vdate-pc {
    margin: 0 auto;
    .vdate-header {
      width: 100%;
      padding: 10px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border: 1px solid #cccccc;
      border-bottom: none;
      .header-black {
        display: flex;
        align-content: center;
        >div{
          cursor: pointer;
        }
      }
    }
    .vdate-content {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      border-top: 1px solid #666;
      border-right: 1px solid #666;
      .v-date-balck {
        cursor: pointer;
        width: 14.28%;
        min-height: 85px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 20px 0;
        text-align: center;
        border-left: 1px solid #666;
        border-bottom: 1px solid #666;
        overflow: hidden;
      }
      .prohibit {
        background-color: #bebebe;
        color: #666;
        cursor: no-drop;
      }
    }
  }
</style>